import { Component } from 'react';
import { connect } from 'react-redux';
import moment from 'moment';
import * as marked from 'marked';

import ReactMarkdown from 'react-markdown'
import { type } from '../../../store/type';
import './index.scss';

class Note extends Component {
  constructor(props) {
    super(props);
    this.props.initData();
  }

  render() {
    const { notes, themes } = this.props;
    let eleTags = (tags) => {
      return tags.map((item, i) => {
        return <span key={i}>
          {item}
        </span>
      });
    }
    const mdTohtml = () => {
      return marked('# Marked in Node.js\n\nRendered by **marked**.');
    }
    let eleNotes = notes.map((item, i) => {
      return <div className="note" title={themes[i % 4]} key={i} style={{ 'backgroundColor': `${themes[i % 4]}44` }}>
        <div className="date">{moment(item.date.replace(/-/ig, ''), "YYYYMMDD").format('MMMM Do YYYY')}</div>
        <div className="text">
          <span class="tags">{eleTags(item.tags)}</span>
          <div dangerouslySetInnerHTML={{ __html: marked(item.text) }} />
        </div>
      </div>
    })
    return <div className="ui-note">
      <div className="notes">
        {eleNotes}
      </div>
    </div>;
  }
}
const mapStateToProps = (state, props) => {
  return {
    notes: state.release.notes,
    themes: state.timeline.themes
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    initData: () => {
      dispatch({ type: type.REFRESH_THEME });
    },
    // refreshThemes: () => { dispatch({ type: type.REFRESH_THEME }); }
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Note);
