import React, { Component } from 'react';
import { connect } from 'react-redux'

import { faSyncAlt } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

import './index.scss';

class Info extends Component {
  constructor(props) {
    super(props);
  }

  _profileKeyToWord(key) {
    let baseWo = 'Base';
    switch (key) {
      case 'experience':
        return '';
      case 'gender':
        return '';
      case 'age':
        return '';
      case 'email':
        return '';
      case 'phone':
        return '';
      case 'keywords':
        return '';
      case 'location':
        return '';
      case 'industry':
        return '';
      case 'jobtype':
        return `${baseWo}`;
      case 'price':
        return `${baseWo}`;
      case 'arrivetime':
        return '';
      default:
        return '';
    }
  }

  render() {
    const { profile } = this.props;
    let eleProfile = Object.keys(profile).map((key) => {
      let value = profile[key];
      if (typeof (value) === 'string') {
        let keyWord = this._profileKeyToWord(key);
        return <li key={key}>
          <span>{keyWord}</span>:&nbsp;
          <span>{profile[key]}</span>
        </li>
      } else if (typeof (value) === 'array') {
        let eleArray = value.map(item => {
          return <span>
            {item.description}
          </span>
        })
        return <li key={key}>
          <span>{key}</span>:&nbsp;
          <span>{eleArray}</span>
        </li>
      }
    })
    return (
      <div className="ui-info" >
        <div className="ui-profile">
          <h5>Profile:</h5>
          <ul>
            {eleProfile}
          </ul>
        </div>
        <div className="ui-languages">
          <h5>Language:</h5>
           todo
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state, props) => {
  return {
    profile: state.timeline.societyRoad.profile
  }
}

const mapDispatchToProps = (dispatch, props) => {
  return {
  }
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Info)
