import { deepCopy } from '../../../util/common';
import { Config } from '../../../resource/timeline';
import { type } from '../../type';

export const initialStateTimeLine = {
  selected: '#1DB702',
  themes: ['#1DB702', '#5C3317', '#9F5F9F', '#855E42', '#D98719', '#FF7F00', '#428BCA'],
  allThemes: Config.allThemes,
  societyRoad: Config.societyRoad,
  navBar: [],
  navIsClick: false
};

export function timeLineReducer(state = initialStateTimeLine, action) {
  switch (action.type) {
    case type.CHANGE_THEME: {
      const item = action.item;
      return {
        ...state,
        selected: item
      };
    }
    case type.REFRESH_THEME: {
      let allThemes = state.allThemes;
      let code = allThemes[Math.floor(Math.random() * allThemes.length)];
      var c1 = code.substring(0, 6);
      var c2 = code.substring(6, 12);
      var c3 = code.substring(12, 18);
      var c4 = code.substring(18, 24);
      var id = code.substring(25, 31);
      const selected = `#${c1}`;
      const themes = [`#${c1}`, `#${c2}`, `#${c3}`, `#${c4}`];
      return {
        ...state,
        selected: selected,
        themes: themes
      };
    }
    case type.FOLD_YEAR: {
      let societyRoad = deepCopy(state.societyRoad); //[...state.societyRoad].projects;
      let projects = societyRoad.projects;
      let item = action.item;
      projects.forEach((project) => {
        if (project.id === item.id) {
          project.fold = item.fold === undefined ? true : !item.fold;
        }
      });
      return {
        ...state,
        societyRoad: societyRoad
      };
    }
    case type.INIT_NAVBAR: {
      const titles = Object.keys(Config.societyRoad);
      let items = [];
      titles.map((title, i) => {
        items.push({
          id: i,
          value: title,
          text: title,
          active: false,
          color: null,
          top: null
        });
      });
      console.log(3, items);
      return {
        ...state,
        navBar: items
      };
    }
    case type.ACTIVE_NAVBAR: {
      let items = deepCopy(state.navBar);
      let scrollTop = document.querySelector('.ui-road').scrollTop;
      let clientHeight = document.querySelector('.ui-road').clientHeight;
      let scrollHeight = document.querySelector('.ui-road').scrollHeight;
      console.log(scrollTop, clientHeight, scrollHeight);
      if (scrollTop <= 50) {
        items.forEach((item, index) => {
          item.active = index === 0 ? true : false;
        });
      } else if (scrollTop + clientHeight >= scrollHeight) {
        items.forEach((item, index) => {
          item.active = index === items.length - 1 ? true : false;
        });
      } else {
        items.forEach((item) => {
          let eleItem = document.querySelector(`.ui-${item.value}`);
          item.top = eleItem ? eleItem.getBoundingClientRect().top : null;
        });
        let dataSort = deepCopy(items).filter((item) => item.top !== null);
        dataSort = dataSort.sort(function (a, b) {
          return Math.abs(a.top) - Math.abs(b.top);
        });
        items.forEach((item) => {
          item.active = item.value === dataSort[0].value;
        });
      }

      return {
        ...state,
        navBar: items
      };
    }
    case type.UPDATE_NAVBAR_STATUS: {
      const navIsClick = action.navIsClick;
      const navBarValue = action.navBarValue;
      console.log(navIsClick, navBarValue);
      if (!navBarValue) {
        return {
          ...state,
          navIsClick: navIsClick
        };
      } else {
        let items = deepCopy(state.navBar);
        items.forEach((item) => {
          item.active = item.value === navBarValue ? true : false;
        });
        return {
          ...state,
          navIsClick: navIsClick,
          navBar: items
        };
      }
    }
    default: {
      return state;
    }
  }
}
