import * as React from 'react';
import './footer.scss';
import moment from 'moment';
import { Config } from '../../../resource/fleece';

export class Footer extends React.Component {
  constructor(props: any) {
    super(props);
    this.state = {
      start: '20130722',
      end: '2018',
      support: Config.orgInfo['profile']['link'],
      powered: Config.orgInfo['profile']['powered']
    }
  }

  componentDidMount() {
    this.setState({
      end: moment().format('YYYYMMDD')
    })
  }

  render() {
    return (
      <div className="footer-component">
        <div className="content-container">
          <span className="copyright">
            &copy; {this.state['start']}-{this.state['end']}  | ver 1.2.20210329
          <br />
          Powered by <a href={this.state['powered']} target="blank">99diary.com</a>. All rights reserved.
          </span>
        </div>
      </div>
    );
  }
}