import React, { Component } from 'react';
import { connect } from 'react-redux'

import { type } from '../../../store/type';
import './index.scss';

class Theme extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    const { selected, themes, changeTheme } = this.props;
    let themesEle = themes.map((item, i) => {
      return <span key={String(i)}
        className="color-block"
        style={{ 'backgroundColor': item, 'opacity': selected == item ? 1 : 0.8 }}
        title={item}
        onClick={() => changeTheme(item)}
      >
      </span>
    });
    return (
      <div className="theme">
        {themesEle}
      </div>
    );
  }
}

const mapStateToProps = (state, props) => {
  return {
    selected: state.timeline.selected,
    themes: state.timeline.themes,
    allthemes: state.timeline.allthemes,
  }
}

const mapDispatchToProps = (dispatch, props) => {
  return {
    changeTheme: (item) => { dispatch({ type: type.CHANGE_THEME, item: item }); }
  }
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Theme)
