import React from 'react';
// import './header.scss';
import { Common } from '../../../resource/common';
import { Navbar, Nav, NavDropdown } from 'react-bootstrap';

export class Header extends React.Component {
  constructor(props: any) {
    super(props);
    this.state = {
      title: Common.siteName,
      entry: {
        menus: this.props['routes']
      },
    };
  }

  componentDidMount() {
    this.showMsg('Mounting componentDidMount');
  }

  componentDidUpdate() {
    this.showMsg('Updating componentDidUpdate');
  }

  // Unmounting
  // This method is called when a component is being removed from the DOM
  componentWillUnmount() {
    this.showMsg('Unmounting componentWillUnmount');
  }

  // Error Handling
  // This method is called when there is an error during rendering, in a lifecycle method, or in the constructor of any child component.
  componentDidCatch() {
    this.showMsg('Error Handling componentDidCatch');
  }

  showMsg(message: string): void {
    console.log(message);/* eslint no-console: 0*/
  }
  render(): any {
    let menus = this.state['entry']['menus'].map((item: Object, i: Number) => {
      let children = item['children'];
      if (children && children.length > 0) {
        let droplist = children.map((child: Object, j: Number) => {
          if (child['component']) {
            return <NavDropdown.Item key={`${String(i)}-${String(j)}`} href={`/#${child['path']}`}>{child['name']}</NavDropdown.Item>
          } else {
            return <NavDropdown.Item key={`${String(i)}-${String(j)}`} href={`${child['path']}`}>{child['name']}</NavDropdown.Item>
          }
        });
        return <NavDropdown key={String(i)} title={item['name']} id="basic-nav-dropdown">
          {droplist}
        </NavDropdown>
      } else {
        return <Nav.Link key={String(i)} href={`/#${item['path']}`}>{item['name']}</Nav.Link>
      }
    });
    return (
      <>
        <Navbar bg="dark" variant="dark">
          <Navbar.Brand href="#">{this.state['title']}</Navbar.Brand>
          <Nav className="mr-auto">
            {menus}
          </Nav>
        </Navbar>
      </>
    );
  }
}