import React, { Component } from 'react';
import { connect } from 'react-redux'
import { faSyncAlt } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import * as $ from 'jquery';

import { type } from '../../store/type';

import Theme from './theme/index';
import Info from './info/index';
import History from './history/index';
import Log from './log/index';

import './index.scss';

class TimeLine extends Component {
  constructor(props) {
    super(props);
    this.scrollRoad = React.createRef();
  }

  _handleScroll() {
    console.log(8, this.props.navIsClick);
    if (this.props.navIsClick) {
      this.props.updateNavBarStatus();
      return false;
    }
    this.props.activeNavBar();
  }

  render() {
    const { selected } = this.props;
    return (
      <div className="ui-road"
        ref={this.scrollRoad}
        style={{ 'color': selected }}
        onScroll={() => this._handleScroll()}
      >
        <p>...</p>
        Change Theme:&nbsp;&nbsp;
        <FontAwesomeIcon className="icon-refresh-theme" icon={faSyncAlt} onClick={() => this.props.refreshThemes()} title="refresh themes" />
        <Theme />
        <hr />
        <Info />
        <hr />
        <History />
        <hr />
        <Log />
      </div>
    );
  }
}


const mapStateToProps = (state, props) => {
  return {
    selected: state.timeline.selected,
    navBarItems: state.timeline.navBar,
    navIsClick: state.timeline.navIsClick
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    refreshThemes: () => { dispatch({ type: type.REFRESH_THEME }); },
    updateNavBarStatus: () => {
      dispatch({ type: type.UPDATE_NAVBAR_STATUS, navIsClick: false, navBarValue: null });
    },
    activeNavBar: () => {
      dispatch({ type: type.ACTIVE_NAVBAR });
    }
  }
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(TimeLine)
