import { combineReducers, createStore } from 'redux';

import { testReducer, initialState } from './modules/fleece';
import { timeLineReducer, initialStateTimeLine } from './modules/timeline';
import { releaseReducer, initialStateRelease } from './modules/release';

const allReducers = combineReducers({
  test: testReducer,
  timeline: timeLineReducer,
  release: releaseReducer
});

const store = createStore(allReducers, {
  test: initialState,
  timeline: initialStateTimeLine,
  release: initialStateRelease
});

export default store;
