import { useState } from 'react';
import { Carousel } from 'react-bootstrap';
import './index.scss'

import { ConfigRelease } from '../../../resource/release';

export function UiCarousel() {
  const [index, setIndex] = useState(0);

  const handleSelect = (selectedIndex, e) => {
    setIndex(selectedIndex);
  };
  console.log(ConfigRelease);
  let eleCarousel = ConfigRelease.carousels.map((item, i) => {
    return <Carousel.Item>
      <img
        key={i}
        className="d-block w-100"
        src={item.src}
        alt={item.date}
        height="240px"
        // width="660px"
      />
    </Carousel.Item>
  });
  let eleVer = ConfigRelease.vers.map((item, i) => {
    return <p>
      <a href={item.link} target="_blank">{item.text}</a>
    </p>
  });
  return (
    <div className="ui-carousel" >
      <Carousel className="ui-carousel" activeIndex={index} onSelect={handleSelect}>
        {eleCarousel}
      </Carousel>
      <div>
        <div className="title">
          :
        </div>
        {eleVer}
      </div>
    </div >

  );
}