import React, { Component } from 'react';
import { connect } from 'react-redux';
import moment from 'moment';

import { faChevronCircleDown, faChevronCircleUp } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

import { type } from '../../../store/type';
import './index.scss';

class History extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    const { selected, companys, projects } = this.props;
    let eleCompanys = companys.map(item => {
      return <li>
        : {item.date} <br />
        : {item.name} <br />
        : {item.people} <br />
        {item.name} <br />
        : {item.industry} <br />
        : {item.description} <br />
        : {item.type}
      </li>
    });

    let eleTag = (tags) => {
      return <div className="tags">{
        tags.map((item, index) => {
          return <span key={index} className="tag">
            {item.description}
          </span>
        })
      }
      </div>
    };

    let eleUrl = (urls) => {
      return urls.map((item, index) => {
        return <a key={index}
          className="link"
          href={`https://tools.99diary.com/#/brige?url=${item.url}`}
          target="_blank">
          {`${index + 1}`}
        </a>
      })
    };

    let eleProjects = (items) => {
      return items.map((item, index) => {
        return <li key={index} className="project" style={{ 'borderColor': selected }}>
          <div><h5>{item.title}</h5></div>
          <div className="p-discribe date" style={{ 'color': selected }}>
            {/* {item.date.start} */}
            {moment(item.date.start).format('MM')}
            ~
            {
              item.date.end.length > 0 &&
              moment(item.date.end).format('MM')
            }
          </div>
          <div className="circle" style={{ 'borderColor': selected }}></div>
          {
            item.description.length > 0 &&
            <div className="p-discribe">
              {item.description}
            </div>
          }
          <div className="p-discribe">: {item.envir}</div>
          <div className="p-discribe">
            :
            {eleUrl(item.urls)}
          </div>
          {
            item.module.length > 0 &&
            <div className="p-discribe">: {item.module}</div>
          }
          {
            item.stacks.length > 0 &&
            eleTag(item.stacks)
          }
        </li>
      });
    };

    let eleYears = projects.map((item, index) => {
      return <div key={index} className="years" style={{ 'borderColor': selected }}>
        <div className="year">
          {item.time}
          {item.fold}
          <FontAwesomeIcon icon={!item.fold ? faChevronCircleDown : faChevronCircleUp} onClick={() => this.props.foldYear(item)} title="fold years" />
        </div>
        {
          item.fold &&
          <div className="pline">
            <ul className="line">
              {eleProjects(item.historys)}
            </ul>
          </div>
        }

      </div>
    });
    return (
      <div className="ui-history">
        <div className="ui-companys">
          <h5>Company</h5>
          <p>
            {/* <ul>
              {eleCompanys}
            </ul> */}
          </p>
        </div>
        <div className="ui-projects">
          <h5>Project</h5>
          <div>
            {eleYears}
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state, props) => {
  return {
    selected: state.timeline.selected,
    companys: state.timeline.societyRoad.companys,
    projects: state.timeline.societyRoad.projects
  }
}

const mapDispatchToProps = (dispatch, props) => {
  return {
    foldYear: (item) => {
      dispatch({ type: type.FOLD_YEAR, item: item });
    }
  }
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(History)
