import React, { Component } from 'react';
import moment from 'moment';
import { connect } from 'react-redux'
import { faSyncAlt } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import * as $ from 'jquery';

import { type } from '../../../store/type';

import './index.scss';

class NavBar extends Component {
  constructor(props) {
    super(props);
    this.props.initData();
    this.state = {
      items: [],
      status: true
    }
  }

  componentDidMount() {
  }

  componentDidUpdate(prevProps, prevState) {
    if (prevState.items !== this.props.items) {
      this.setState({ items: this.props.items });
    }
  }

  setFoldStatus(status) {
    this.setState({ status: status });
  }

  render() {
    let selected = this.props.selected;
    let ele = this.state.items.map((item, i) => {
      return <li
        key={i}
        className={item.active ? 'active' : ''}
        onClick={() => { this.props.scrollToEle(item.value); }}
      >
        {
          this.state.status ?
            <span className="line"
              style={item.active ? { 'backgroundColor': selected } : {}}
              title={String(item.active) + item.top}></span> :
            <span
              style={
                item.active ? { 'color': selected } : {}
              }
            >
              {item.value}
            </span>
        }
      </li >
    })
    return (
      <div className="ui-navbar" >
        <div
          className={`nav ${this.state.status ? 'fold' : 'unfold'}`}
          onMouseEnter={() => this.setFoldStatus(false)} onMouseLeave={() => this.setFoldStatus(true)}>
          <ul>
            {ele}
          </ul>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state, props) => {
  return {
    selected: state.timeline.selected,
    societyRoad: state.timeline.societyRoad,
    items: state.timeline.navBar,
    navIsClick: state.timeline.navIsClick
  }
}

const mapDispatchToProps = (dispatch, props) => {
  return {
    initData: () => {
      dispatch({ type: type.REFRESH_THEME });
      dispatch({ type: type.INIT_NAVBAR });
    },
    scrollToEle: (className) => {
      const eleContainer = $('.ui-road');
      const eleContainerPara = eleContainer.find(`.ui-${className}`);
      const eleHeader = 53;
      const scrollTop = eleContainer.scrollTop();
      if (eleContainerPara.length === 0) {
        console.log('This element hidden');
        return;
      }
      const top = eleContainerPara[0].offsetTop;

      const cubic = (value) => Math.pow(value, 3);
      const easeInOutCubic = (value) => (value < 0.5 ? cubic(value * 2) / 2 : 1 - cubic((1 - value) * 2) / 2);

      const beginTime = Date.now();
      const beginValue = top - scrollTop;
      const rAF = window.requestAnimationFrame || ((func) => setTimeout(func, 16));
      const frameFunc = () => {
        const progress = (Date.now() - beginTime) / 500;
        if (progress < 1) {
          dispatch({ type: type.UPDATE_NAVBAR_STATUS, navIsClick: true, navBarValue: className });
          eleContainer.scrollTop(scrollTop + beginValue * easeInOutCubic(progress) + eleHeader);
          rAF(frameFunc);
        } else {
          eleContainer.scrollTop(scrollTop + beginValue + eleHeader);
        }
      };
      rAF(frameFunc);
    }
  }
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(NavBar)
